import { NodesService } from "../services/nodes.service";
declare let draw2d: any;

import {
  Component,
  OnInit, Output, EventEmitter
} from '@angular/core';

@Component({
  selector: 'nodes',
  providers: [],
  template: require('./nodes.component.html')
})

export class NodesComponent {
  @Output() public addNode = new EventEmitter();
  public nodes: any;

  constructor(private nodesService: NodesService){
    this.nodes = nodesService.getNodeTypes();
  }

  public onAddNode(type) {
    this.addNode.emit(type);
  }

  public getNodeByType(type): any {
    let nodeType = this.nodesService.getNodeType(type);
    if(!nodeType) {
      throw new Error(`Неизвестный тип узла: '${type}'`);
    }
    return new (nodeType.className)();
  }
}



