import { NodeParameter } from "../models/node-parameter";
declare let draw2d: any;
declare let $: any;
import { Injectable } from "@angular/core";

@Injectable()
export class NodesService {
  public getNodeTypes() {
    return this.nodes;
  }

  public getNodeType(type) {
    if (!type) {
      throw new Error('Не задан тип узла');
    }
    let nodeType = this.nodes.filter((x) => x.name === type);
    if (nodeType.length === 0) {
      throw new Error(`Неизвестный тип узла: '${type}'`);
    }
    return nodeType[0];
  }

  public getNodeInstance(type) {
    let nodeType = this.getNodeType(type);
    return new (nodeType.className)();
  }

  public getTypeByInstance(node): any {
    let nodeType = this.nodes.filter((x) => node instanceof x.className);
    if (nodeType.length === 0) {
      return 0;
    }
    return nodeType[0];
  }

  public nodeTypeLabelByInstance(node) {
    let nodeType = this.getTypeByInstance(node);
    if(!nodeType) {
      return "";
    }
    return nodeType.label;
  }

  public getDefaultUserData(type) {
    let params = [];
    params.push(new NodeParameter({name: 'name', label: 'Имя', value: 'Без имени'}));
    if(['operation', 'product', 'sync'].indexOf(type) !== -1) {
      params.push(new NodeParameter({name: 'cipher', label: 'Шифр', value: ''}));
    }
    if('operation' === type) {
      params.push(new NodeParameter({name: 'tools', label: 'Инструменты', value: ''}));
    }
    return params;
  }

  public getPropertyValue(node, propertyName) {
    let prop = node.userData.parameters.filter(x => x.name === propertyName);
    if(prop.length === 0) {
      throw new Error(`Property not found: ${propertyName}`);
    }
    return prop[0].value;
  }

  private nodes = [
    {name: 'start', label: 'Начало', className: draw2d.shape.node.Start},
    {name: 'end', label: 'Конец', className: draw2d.shape.node.End},
    {name: 'operation', label: 'Операция', className: draw2d.shape.node.Operation},
    {name: 'condition', label: 'Условие', className: draw2d.shape.node.Condition},
    {name: 'sync', label: 'Требование', className: draw2d.shape.node.Sync},
    {name: 'product', label: 'Продукт', className: draw2d.shape.node.Product},
    {name: 'block', label: 'Блок', className: draw2d.shape.node.Block},
    {name: 'connection', label: 'Соединение', className: draw2d.Connection, hidden: true},
    {name: 'label-handle', label: 'Выноска', className: draw2d.shape.basic.LabelHandle},
    //{name: '', label: ''}
  ];
}



