import { NodesService } from "../services/nodes.service";
declare let draw2d: any;
declare let $: any;

import {
  Component,
  OnInit
} from '@angular/core';

import { NodesComponent } from '../nodes/nodes.component';
import { SampleService } from "../services/sample.service";

@Component({
  selector: 'home',  // <home></home>
  providers: [],
  styles: [require('./home.component.css')],
  template: require('./home.component.html')
})
export class HomeComponent implements OnInit {
  public selectedNode;
  public currentModelPart = 'main';
  public portsVisible = true;
  public gridSnapEnabled = false;

  private SAVED_MODEL_KEY = 'model';
  private GRID_STEP = 30;
  private canvas;


  constructor(private nodesService: NodesService) {
  }

  public ngOnInit() {
    this.canvas = new draw2d.Canvas('gfx_holder', 2121, 1500);
    ((svg) => {
      svg.style.position = 'relative';
      svg.style.backgroundColor = '#FFFFFF'; // '#fff9dc';
      svg.style.border = '1px solid darkolivegreen';
    })(document.querySelectorAll('svg')[0]);

    this.canvas.installEditPolicy(new draw2d.policy.connection.OrthogonalConnectionCreatePolicy());
    this.toggleGridSnap();
    this.canvas.uninstallEditPolicy('draw2d.policy.figure.RegionEditPolicy');
    this.load();
    this.canvas.on('select', (emitter, event) => {
      this.selectedNode = event.figure;
      if (event.figure != null) {
        console.log(event.figure);
      }
    });
    this.canvas.on('connect', (emitter, event) => {
      if (!event) {
        return;
      }
      let c = event.connection;
      if (!(c instanceof draw2d.Connection)) {
        return;
      }
      c.setStroke(1);
      c.setColor('#000000');
    });
  }

  public toggleGridSnap() {
    if(this.gridSnapEnabled) {
      this.canvas.uninstallEditPolicy('draw2d.policy.canvas.SnapToGridEditPolicy');
    } else {
      let gridPolicy = new draw2d.policy.canvas.SnapToGridEditPolicy(this.GRID_STEP);
      gridPolicy.setGridColor(null);
      this.canvas.installEditPolicy(gridPolicy);
    }
    this.gridSnapEnabled = !this.gridSnapEnabled;
  }

  public setRadius(connection, radius) {
    try {
      connection.setRadius(radius);
    } catch (e) {
      alert('Не удалось установить радиус:' + e.message);
    }
  }

  //noinspection JSMethodCanBeStatic
  public isConnection(node) {
    return node instanceof draw2d.Connection;
  }

  //noinspection JSMethodCanBeStatic
  public isBlock(node) {
    return node instanceof draw2d.shape.node.Block;
  }

  //noinspection JSMethodCanBeStatic
  public editBlock(node) {
    this.save();
    this.currentModelPart = 'block_' + node.id;
    try {
      this.load();
    } catch (e) {
      this.canvas.clear();
    }
  }

  public backToMainModel() {
    this.save();
    this.currentModelPart = 'main';
    this.load();
  }

  //noinspection JSMethodCanBeStatic
  public setArrows(node) {
    let decorator = new draw2d.decoration.connection.connectionPikArrowDecorator();
    if (node.sourceDecorator) {
      node.setSourceDecorator(null);
      node.setTargetDecorator(decorator);
    } else {
      node.setSourceDecorator(decorator);
      node.setTargetDecorator(null);
    }
  }

  public save() {
    let writer = new draw2d.io.json.Writer();
    writer.marshal(this.canvas, (json) => {
      this.saveModelPart(this.currentModelPart, json);
    });
  }

  public load() {
    let reader = new draw2d.io.json.Reader();
    let json = this.loadModelPart(this.currentModelPart);
    this.canvas.clear();
    reader.unmarshal(this.canvas, json);
    let antEditPolicy = new draw2d.policy.figure.AntSelectionFeedbackPolicy();
    let figures = this.canvas.getFigures();
    figures.data.forEach((f) => f.installEditPolicy(antEditPolicy));
  }

  public reset() {
    if (!confirm('Сбросить модель?')) {
      return;
    }
    for (let i = 0; i < localStorage.length; i++) {
      localStorage.removeItem(localStorage.key(i));
    }
    window.location.reload();
  }

  public createSampleModel() {
      this.saveModelPart('main', SampleService.sample());
      window.location.reload();
  }

  public addNode(type) {
    let node = this.nodesService.getNodeInstance(type);
    node.userData = {parameters: this.nodesService.getDefaultUserData(type)};
    node.installEditPolicy(new draw2d.policy.figure.AntSelectionFeedbackPolicy());
    this.canvas.add(node);
  }

  public toggleCipherLabel(node) {
    if(typeof node !== 'object') {
      return;
    }
    let labels = node.getChildren().data.filter(x => x instanceof draw2d.shape.basic.CipherLabel);
    if(labels.length > 0) {
      labels.forEach(x => node.remove(x));
    } else {
      let s = new draw2d.shape.basic.CipherLabel();
      s.setText(this.nodesService.getPropertyValue(node, 'cipher'));
      node.add(s, new draw2d.layout.locator.TopLeftLocator());
    }
  }

  public canAddCipher(node) {
    return node && node instanceof draw2d.shape.node.Operation;
  }

  public togglePorts() {
    this.portsVisible = !this.portsVisible;
    this.canvas.getAllPorts().data.forEach((x) => x.setVisible(this.portsVisible));
  }

  public addLabel(node) {
    let label = new draw2d.shape.note.PostIt({text:"Метка", x:20, y:20});
    label.installEditor(new draw2d.ui.LabelInplaceEditor());
    label.setFontColor(0);
    label.setColor(null);
    label.setBackgroundColor(null);
    node.add(label, new draw2d.layout.locator.DraggableLocator());
  }

  private saveModelPart(partName, json) {
    localStorage.setItem(`${this.SAVED_MODEL_KEY}.${partName}`, JSON.stringify(json, null, 2));
  }

  private loadModelPart(partName) {
    let jsonString = localStorage.getItem(`${this.SAVED_MODEL_KEY}.${partName}`);
    if (!jsonString) {
      return null;
    }
    try {
      return JSON.parse(jsonString);
    } catch (e) {
      alert(`Ошибка формата сохраненных даных: ${e.message}`);
    }
  }
}



