import {
  Component,
  OnInit, Output, EventEmitter, Input
} from '@angular/core';
import { NodesService } from "../services/nodes.service";

@Component({
  selector: 'node-info',
  providers: [],
  template: require('./node-info.component.html')
})
export class NodeInfoComponent {
  private _node;
  @Input()
  set node(node) {
    this._node = node;
  }

  get node() {
    return this._node;
  }

  constructor(private nodesService: NodesService) {

  }

  public typeLabel(node) {
    return this.nodesService.nodeTypeLabelByInstance(node);
  }
}



